@import '../../styles/breakpoints';

// --sb-panel-layout-col-gap
// --sb-panel-layout-row-gap

.sb-panel-layout {
  // NOTE: Ensure property fallback to previous size if not set
  --sb-panel-layout-small-areas: var(--sb-panel-layout-slim-areas);
  --sb-panel-layout-small-columns: var(--sb-panel-layout-slim-columns);
  --sb-panel-layout-small-rows: var(--sb-panel-layout-slim-rows);

  --sb-panel-layout-medium-areas: var(--sb-panel-layout-small-areas);
  --sb-panel-layout-medium-columns: var(--sb-panel-layout-small-columns);
  --sb-panel-layout-medium-rows: var(--sb-panel-layout-small-rows);

  --sb-panel-layout-large-areas: var(--sb-panel-layout-medium-areas);
  --sb-panel-layout-large-columns: var(--sb-panel-layout-medium-columns);
  --sb-panel-layout-large-rows: var(--sb-panel-layout-medium-rows);

  --sb-panel-layout-wide-areas: var(--sb-panel-layout-large-areas);
  --sb-panel-layout-wide-columns: var(--sb-panel-layout-large-columns);
  --sb-panel-layout-wide-rows: var(--sb-panel-layout-large-rows);

  display: grid;
  gap: var(--sb-panel-layout-col-gap);

  grid-template-areas: var(--sb-panel-layout-slim-areas);
  grid-template-columns: var(--sb-panel-layout-slim-columns);
  grid-template-rows: var(--sb-panel-layout-slim-rows);
  height: 100%;

  // NOTE: adding overflow auto since a column can be wider than the others if
  // content doesn't fit, thus breaking the layout.
  overflow: auto;
  row-gap: var(--sb-panel-layout-row-gap);

  @include window-width-min-small {
    grid-template-areas: var(--sb-panel-layout-small-areas);
    grid-template-columns: var(--sb-panel-layout-small-columns);
    grid-template-rows: var(--sb-panel-layout-small-rows);
  }

  @include window-width-min-medium {
    grid-template-areas: var(--sb-panel-layout-medium-areas);
    grid-template-columns: var(--sb-panel-layout-medium-columns);
    grid-template-rows: var(--sb-panel-layout-medium-rows);
  }

  @include window-width-min-large {
    grid-template-areas: var(--sb-panel-layout-large-areas);
    grid-template-columns: var(--sb-panel-layout-large-columns);
    grid-template-rows: var(--sb-panel-layout-large-rows);
  }

  @include window-width-min-wide {
    grid-template-areas: var(--sb-panel-layout-wide-areas);
    grid-template-columns: var(--sb-panel-layout-wide-columns);
    grid-template-rows: var(--sb-panel-layout-wide-rows);
  }
}
