@import '../../styles/breakpoints';

.sb-panel {
  grid-area: var(--sb-panel-layout__panel-name);
}

.sb-panel_scrollable,
.sb-panel_auto {
  overflow-y: auto;
}

.sb-panel_scroll {
  overflow-y: scroll;
}

.sb-panel_hidden {
  overflow-y: hidden;
}

.sb-panel__visible-on-wide-up {
  @include window-width-max-wide {
    display: none;
  }
}

.sb-panel__visible-on-large-up {
  @include window-width-max-large {
    display: none;
  }
}

.sb-panel__visible-on-medium-up {
  @include window-width-max-medium {
    display: none;
  }
}

.sb-panel__visible-on-small-up {
  @include window-width-max-small {
    display: none;
  }
}

.sb-panel__visible-on-large-down {
  @include window-width-min-wide {
    display: none;
  }
}

.sb-panel__visible-on-medium-down {
  @include window-width-min-large {
    display: none;
  }
}

.sb-panel__visible-on-small-down {
  @include window-width-min-medium {
    display: none;
  }
}

.sb-panel__visible-on-slim-down {
  @include window-width-min-small {
    display: none;
  }
}
