@import '../../styles/breakpoints';

// SB Modal:
// --sb-modal-z-index - Sets z index of the modal
// --sb-modal-close-btn-color - Close button text color
// --sb-modal-close-btn-hover-color - Close button hover color

.sb-modal {
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 0;
  padding: 20px;
  position: fixed;
  right: 0;
  top: 0;
  z-index: var(--sb-modal-z-index, var(--sbt-stack-modal));
}

.sb-modal__dialog {
  background-color: #fff;
  border-radius: var(--sbt-border-radius-large);
  box-shadow: 0 31px 41px 0 rgba(32, 42, 53, 0.2),
    0 2px 16px 0 rgba(32, 42, 54, 0.08);
  display: grid;
  grid-template-areas:
    'header'
    'body'
    'footer';
  grid-template-rows: auto minmax(0, 1fr) auto;
  max-height: calc(100vh - 100px);
  max-width: 50rem;
  position: relative;
  width: 100%;
  z-index: var(--sb-modal-z-index, var(--sbt-stack-modal));
  @include window-width-min-small() {
    max-height: 692px;
  }
}

.sb-modal__close {
  background-color: transparent;
  border-color: transparent;
  color: var(--sb-modal-close-btn-color, var(--sb-button-color));
  position: absolute;
  right: 0;
  top: 0;

  &:hover {
    background-color: transparent;
    color: var(--sb-modal-close-btn-hover-color, var(--sb-button-color-dark));
  }

  &:focus {
    box-shadow: none;
  }
}

.sb-modal__dialog__header {
  align-items: center;
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-30);
  display: flex;
  justify-content: space-between;
  margin: auto 0;
  min-height: 54px;

  h2 {
    font-family: var(--sbt-font-stack-alt);
    font-size: var(--sbt-font-size-50);
    font-weight: var(--sbt-font-weight-60);
    line-height: var(--sbt-line-height-100);
    padding: 0 1rem;
  }
}

.sb-modal__dialog__body {
  min-height: 4rem;
  overflow: auto;
  padding: var(--sb-modal-body-padding, var(--sbt-spacing-40));

  p {
    margin: 0;
  }
}

.sb-modal__dialog__body_hide-header {
  min-height: 54px;
}

.sb-modal__dialog__footer {
  border-radius: 0 0 var(--sbt-border-radius-large)
    var(--sbt-border-radius-large);
  border-top: 1px solid var(--sbt-theme-tertiary-color-30);
  display: flex;
  padding: 0.5rem;

  &--align-left {
    justify-content: flex-start;
  }

  &--align-space {
    justify-content: space-between;
  }
}

.sb-modal__dialog__footer,
.sb-modal__dialog__header {
  &--align-center {
    justify-content: center;
  }

  &--align-right {
    justify-content: flex-end;
  }
}

.sb-modal__dialog__secondary-button + .sb-modal__dialog__primary-button {
  margin-left: 0.5rem;
}

//Horizontal Alignments
.sb-modal__hor-align-left {
  align-items: flex-start;
}

.sb-modal__hor-align-center {
  align-items: center;
}

.sb-modal__hor-align-right {
  align-items: flex-end;
}
//Vertical Alignments
.sb-modal__ver-align-top {
  justify-content: flex-start;
}

.sb-modal__ver-align-center {
  justify-content: center;
}

.sb-modal__ver-align-bottom {
  justify-content: flex-end;
}
